import React from 'react';
import classNames from 'classnames';
import Head from 'next/head';
import { Button, Headline } from '../../components';
import styles from './Header.css';

type Props = {
  initialLevel?: number;
  small?: boolean;
  description?: string;
  title?: string;
};

export default function Header({
  initialLevel = 1,
  small,
  description = 'Amerikanische Chiropraktik Hamburg',
  title,
}: Props) {
  const pageTitle = [title].filter(Boolean).join(' - ');

  return (
    <header
      className={classNames(styles.header, small && styles.small)}
      style={small ? {} : { backgroundImage: `url('static/banner.jpg')` }}
    >
      <Head>
        <title>{pageTitle}</title>
        <meta name="description" content={description} />
        <meta property="og:description" content={description} />
        <meta property="og:title" content={pageTitle} />
        <meta property="og:image:width" content="1200" />
        <meta property="og:image:height" content="630" />
        <meta property="og:site_name" content={pageTitle} />
        <meta name="twitter:description" content={description} />
      </Head>
      <div className={styles.headlineWrap}>
        <Headline level={1} as={initialLevel} className={styles.headline}>
          Thomas Grossmann
        </Headline>
        <br />
        {/* <Headline level={2} className={styles.headline}>
          Gesundheit von Innen
        </Headline>
        <br /> */}
        <Headline level={3} className={styles.headline}>
          Amerikanische Chiropraktik Hamburg
        </Headline>
      </div>
      {small ? null : (
        <div className={styles.buttonWrap}>
          <Button center href="tel:00494085373395">
            Anrufen
          </Button>
          <Button center href="mailto:info@institut-grossmann.com">
            E-Mail schreiben
          </Button>
        </div>
      )}
    </header>
  );
}
